  @import 'function';
  @import 'base';
  @import 'var';
  @import 'keyframe'; 

  a, a:hover, a:focus, a:active {
    text-decoration: none;
    color: inherit;
    outline: none;
  }
  
  ol, ul, li {
    padding: 0;
    list-style: none;
    margin: 0;
  }
  
  label {
    font-weight: normal;
  }
  
  input[type=text], input[type=email], input[type=password] {
    padding-left: 12px;
    padding-right: 12px;
  }
  
  select {
    padding-left: 8px;
  }
  
  input[type=number] {
    padding-left: 5px;
  }
  
  textarea {
    padding: 8px 12px 5px 12px;
  }
  
  input, textarea, select, button, a {
    outline: none;
  }
  
  button {
    border: none;
    background: none;
    outline: none;
    -webkit-user-select: none;
    user-select: none;
  }
  
  img {
    max-width: 100%;
    vertical-align: middle;
    -webkit-object-fit: cover;
    -moz-object-fit: cover;
    -ms-object-fit: cover;
    object-fit: cover;
  }
  
  .relative {
    position: relative;
  }
  
  .static {
    position: static;
  }
  
  .absolute, .ef-middle:before {
    position: absolute;
  }
  
  .clearfix:before, .clearfix:after {
    content: "";
    display: block;
    clear: both;
  }
  
  @keyframes lac {
    0% {
      transform: rotate(0deg);
    }
    10% {
      transform: rotate(-25deg);
    }
    20% {
      transform: rotate(25deg);
    }
    30% {
      transform: rotate(-25deg);
    }
    40% {
      transform: rotate(25deg);
    }
    50% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }
  
  @keyframes zoomin {
    0% {
      transform: scale(0.8);
      opacity: 1;
    }
    100% {
      transform: scale(2.2);
      opacity: 0.1;
    }
  }
  
  @keyframes zoomout {
    0% {
      transform: scale(0.8);
      opacity: 0.8;
    }
    100% {
      transform: scale(1.1);
      opacity: 0.3;
    }
  }
  
  @keyframes xoay {
    0% {
      transform: skewX(163deg) scaleX(1.1) translate(-39px, 15px) rotateX(0deg);
    }
    50% {
      transform: skewX(163deg) scaleX(1.1) translate(-39px, 15px) rotateX(180deg);
    }
    100% {
      transform: skewX(163deg) scaleX(1.1) translate(-39px, 15px) rotateX(360deg);
    }
  }
  
  @keyframes laolen {
    0% {
      transform: translate(0, -15px);
    }
    50% {
      transform: translate(0, 0);
    }
    100% {
      transform: translate(0, -15px);
    }
  }
  
  @keyframes cross {
    0% {
      left: -110%;
      top: 90%;
    }
    50% {
      left: -10%;
      top: -30%;
    }
    100% {
      left: -10%;
      top: -10%;
    }
  }
  
  @keyframes c-rotateZ {
    0% {
      transform: rotateZ(0deg);
    }
    100% {
      transform: rotateZ(360deg);
    }
  }
  
  @keyframes c-zoom {
    0% {
      transform: scale(1);
      opacity: 1;
    }
    50% {
      transform: scale(1.1);
      opacity: 0.5;
    }
    100% {
      transform: scale(1.2);
      opacity: 0;
    }
  }
  
