  .menu ul li {
    padding-top: 2.4375rem;
  }
  
  .menu ul li a::before {
    content: "";
    display: block;
    width: 0%;
    height: 0.1875rem;
    background-color: #fce0b9;
    position: absolute;
    top: 100%;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    -webkit-transition: 0.3s;
    transition: 0.3s;
  }
  
  .menu ul li a:hover {
    color: #fce0b9;
  }
  
